import React from 'react';
import debounce from 'debounce'
import { Provider, themes, Button, Flex, FlexItem, Dropdown, Input, Header, Label} from '@fluentui/react-northstar';
import { CloseIcon, AcceptIcon } from '@fluentui/react-icons-northstar';


const styles={
    mainPadding:{padding:"0px 2em"},
    mainSidePadding:{padding:"10px 2em"},
    mainPdf:{display:"flex", justifyContent:"center"},
    mainTable:{
        backgroundColor: "#fff",
        border: "1px solid #d8e2f0",
        borderRadius: "4px",
        boxShadow: "0 0 10px 0 rgba(19,29,41,.05)",
        position: "relative",
        padding: "0em 3em 2em"
    },
    backButton:{
        cusor:"pointer"
    },
    label:{
        fontWeight:"bold"
    }
}
class Config extends React.Component{
    constructor(){
        super();
        if(this.microsoftTeams)
            this.microsoftTeams.initialize();
        this.state ={
            apiKey:"",
            adminId:"",
            admins:null,
            admin:null,
            contacts:null,
            contact:null
        }
        this.handleChange = this.handleChange.bind(this)
        this.submit = this.submit.bind(this)
        this.getOptions = this.getOptions.bind(this)
        this.handleContact = this.handleContact.bind(this)
        this.save = this.save.bind(this)
        this.getAdmin = debounce(this.getAdmin,2000).bind(this)
        this.handleToken = this.handleToken.bind(this)
        this.handleAdmin = this.handleAdmin.bind(this)
    }

    async getAdmin(apiKey){
        try{
            const res = await fetch(
                `https://moneybirdhandlerproxy.azurewebsites.net/api/MoneyBirdProxy`,
                {
                    mode:"cors",
                    method:"POST",
                    body: JSON.stringify({
                        method:"GET",
                        path:`administrations.json`,
                        key: apiKey
                    })
                }
            )
            const json = await res.json()
            console.log({json})
            if(json.error)
                this.setState({admins:false})
            else
                this.setState({admins:json})
        }
        catch(e){
            console.log(e)
        }
    }

    componentDidMount(){
        this.microsoftTeams = window? window.microsoftTeams: null;
    }

    getOptions(){
        const {contacts} = this.state;
        return contacts.map(c=>({content:c.company_name, value:c.id}))
    }

    handleContact(e, value){
        this.setState({ contact: value.value }, this.save)
    }

    handleAdmin(e, value){
        this.setState({ admin: value.value })
        this.submit(value.value)
    }

  
    handleChange(event) {
        this.setState({[event.target.name]: event.target.value});
    }
    handleToken(e){
        this.handleChange(e);
        this.getAdmin(e.target.value);

    }
    async submit({value :adminId}){
        console.log(adminId)
        const {apiKey} = this.state;

        this.microsoftTeams.initialize();
        microsoftTeams.getContext(async context=>{
            const {tid} = context;
            try{
                const res = await fetch(
                    `https://moneybirdhandlerproxy.azurewebsites.net/api/MoneyBirdProxy`,
                    {
                        mode:"cors",
                        method:"POST",
                        body: JSON.stringify({
                            method:"GET",
                            path:`${adminId}/contacts.json`,
                            key: apiKey,
                            tid
                        })
                    }
                )
                const json = await res.json()
                console.log({json})
                this.setState({contacts:json})
    
            }
            catch(e){
                console.log("BOOM!")
                console.log(e)
            }
        })
    }

    save(){
        
        const {apiKey, admin:{value: adminId}, contact} = this.state;
        this.microsoftTeams.initialize();
        this.microsoftTeams.settings.setValidityState(true);
        this.microsoftTeams.settings.registerOnSaveHandler((saveEvent) => {
            this.microsoftTeams.settings.setSettings({
                websiteUrl: "https://jp5525.github.io/teams-demo/nl/",
                contentUrl: "https://jp5525.github.io/teams-demo/nl/",
                entityId: JSON.stringify({apiKey, adminId, contact}),
                suggestedDisplayName: "MoneyBird Invoices"
            });
            saveEvent.notifySuccess();
        });
    }

    render(){
        const {contacts, contact, admins, admin } = this.state
        return (
            
                <Provider theme={themes.teams}>
                    <div style={styles.mainPadding}>
                        <div style={styles.mainTable}>
                            <Header content="MoneyBird Opstelling" />
                            <Flex gap="gap.small" vAlign="center" hAlign="start">
                                <FlexItem justifyContent="center">
                                    <form onSubmit={this.submit} style={{width:"100%"}}>
                                        
                                        <label style={styles.label} >API Token</label> <br/>
                                        <span>TDit is het API-token voor u MoneyBird-account. Je kunt er een genereren <a target="_blank" href="https://moneybird.com/user/applications/new">hier</a> voor persoonlijk gebruik.</span>
                                        {admins== false? <> <br/><Label content="API-token niet geldig" icon={{as: CloseIcon}}/></> : null}
                                        {admins? <> <br/><Label content="API-token IS geldig" icon={{as: AcceptIcon}}/></> : null}
                                        <Input type="text" placeholder="API-token" name="apiKey" id="apiKey" onChange={this.handleToken} fluid={true}/> <br />
                                        
                                        <br />

                                        <label style={styles.label} >Toediening</label> <br/>
                                        <span>Dit is de administratie van waaruit u contacten wilt bekijken</span>
                                        <Dropdown
                                            items={admins? admins.map(a=>({content:a.name, value:a.id})): []}
                                            value={admin? admin.content : null}
                                            placeholder='Administratie'
                                            onChange={this.handleAdmin}
                                            name="Admin"
                                            disabled={admins? false: true}
                                            fluid={true}
                                            />

                                        <br />

                                        <label style={styles.label} >Contact</label> <br/>
                                        <span>De contactpersoon wiens facturen verschijnen op de factuurtabs</span>
                                        <Dropdown
                                            items={contacts? this.getOptions(): []}
                                            value={contact? contact.content : null}
                                            placeholder='Contacts'
                                            onChange={this.handleContact}
                                            name="contact"
                                            disabled={contacts? false: true}
                                            />
                
                                    </form>
                                </FlexItem>
                            </Flex>
                        </div>
                    </div>
                </Provider>
            
            
        )

    }
}

export default Config;
